
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {track, status} from '../config'
export default {
  name: 'track',
  head: {
    title: {
      inner: '订单跟踪'
    }
  },
  components: {
    Timeline,
    TimelineItem
  },
  data () {
    return {
      list: [],
      status: JSON.parse(this.$localStorage.get('orderDetail')).orderStatus
    }
  },
  created () {
    this.list = this.list.reverse()
    this.getList()
  },
  methods: {
    getList () {
      this.$http({
        method: 'jsonp',
        url: track,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          userId: this.$route.params.userId,
          orderId: this.$route.params.orderId
        }
      })
      .then(res => {
        console.log(res)
        this.list = res.body.data.logList
        this.list.forEach(el => {
          el.text = status[el.status]
          el.createTime = dateFormat(el.createTime)
        })
      })
    }
  }
}



